import React from "react";
import { Link, withRouter } from "react-router-dom";

import IconButton from "../IconButton/IconButton";

import "./Menu.scss";

const Menu = (props) => {
  let className = "Menu";
  const routeParts = props.location.pathname.split("/");
  const logoOnly = routeParts[1] === "herb";

  if (logoOnly) className += " logoOnly";

  return (
    <nav className={className}>
      <Link to="/" className="LogoIcon">
        <img src="/images/ui/logo.svg" alt="  " />
      </Link>
      <div className="IconButtonContainer">
        <Link to="/recipes">
          <IconButton icon="recipes" label="" />
        </Link>
        <Link to="/intro">
          <IconButton icon="help" label=" ?" />
        </Link>
        <Link to="/about">
          <IconButton icon="about" label="" />
        </Link>
      </div>
    </nav>
  );
};

export default withRouter(Menu);
